import React from 'react'
import {render} from 'react-dom'

import {Contacts, HelloWorld} from '../../src'

let Demo = React.createClass({
  render() {
    return <div>
      <h1>ioc-liturgical-react Demo</h1>
      <Contacts/>
      <HelloWorld/>
    </div>
  }
})

render(<Demo/>, document.querySelector('#demo'))



